/* eslint-disable prettier/prettier */
<template>
  <button :disabled="isDisabled" class="btn mb-0" :class="getClasses(variant, color, size, fullWidth, active)">
    <slot v-if="!isLoading" />
    <div v-if="isLoading" class="spinner-border spinner-border-sm" role="status"></div>
  </button>
</template>

<script>
  export default {
    name: 'component-button',
    props: {
      color: { type: String, default: 'info' },
      size: { type: String, default: 'md' },
      variant: { type: String, default: 'fill' },
      fullWidth: { type: Boolean, default: false },
      active: { type: Boolean, default: false },
      isLoading: { Type: Boolean, default: false },
      isDisabled: { Type: Boolean, default: false },
    },
    methods: {
      getClasses: (variant, color, size, fullWidth, active) => {
        let colorValue, sizeValue, fullWidthValue, activeValue;
        switch (variant) {
          case 'gradient':
            colorValue = `bg-gradient-${color}`;
            break;
          case 'outline':
            colorValue = `btn-outline-${color}`;
            break;
          default:
            colorValue = `btn-${color}`;
            break;
        }

        sizeValue = size ? `btn-${size}` : null;
        fullWidthValue = fullWidth ? `w-100` : null;
        activeValue = active ? `active` : null;
        return `${colorValue} ${sizeValue} ${fullWidthValue} ${activeValue}`;
      },
    },
  };
</script>
