<template>
  <div>
    <CustomersTable :customers="customersList.data" />
    <PaginationComponent
      v-if="customersList.totalCount"
      class="mt-4"
      :pageCount="Math.ceil(customersList.totalCount / customersList.pageSize)"
      :perPage="pagination.pageSize"
      :value="currentPage"
      :align="'center'"
      @input="handlePageChange"
    />
  </div>
</template>

<script>
  import CustomersTable from './customer-table';
  import PaginationComponent from '@/components/Pagination';

  export default {
    name: 'customers-page',
    components: { CustomersTable, PaginationComponent },
    data() {
      return {
        pagination: { pageSize: 10, pageNumber: 1, currentPage: 1 },
      };
    },

    mounted() {
      this.$store.dispatch('CustomersStore/loadCustomersList', {
        pageNumber: this.pagination.pageNumber,
        pageSize: this.pagination.pageSize,
      });
    },
    computed: {
      customersList() {
        return this.$store.state.CustomersStore.customersList;
      },
    },
    methods: {
      handlePageChange(newPage) {
        this.currentPage = newPage;
        this.$store.dispatch('CustomersStore/loadCustomersList', {
          pageNumber: this.pagination.pageNumber * this.currentPage,
          pageSize: this.pagination.pageSize,
        });
      },
    },
  };
</script>
