<template>
  <div class="card min-vh-50">
    <div class="card-header pb-0">
      <div class="d-flex justify-content-between">
        <div class="d-flex gap-4 align-items-center">
          <h4>Customers</h4>
          <div v-if="tableProgress.loading" class="spinner-border spinner-border" role="status"></div>
        </div>
        <button-component
          color="success"
          size="sm"
          variant="outline"
          :isLoading="tableProgress.loading"
          :isDisabled="tableProgress.loading"
          @click="toggleAddModal()"
        >
          Add Customer
        </button-component>
      </div>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0">
        <table v-if="!tableProgress.loading" class="table align-items-center mb-0">
          <thead>
            <tr>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Username / Email</th>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2">Name / Phone</th>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Create Date</th>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Update Date</th>
              <th class="text-secondary opacity-7"></th>
              <th class="text-secondary opacity-7"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="customer in customers" :key="customer.id">
              <td>
                <div class="d-flex px-2 py-1">
                  <div>
                    <object
                      :data="'http://testingnbackend.somee.com' + customer.imageUrl"
                      type="image/png"
                      class="avatar avatar-sm me-3"
                    >
                      <img src="../../assets/img/team-2.jpg" alt="Stack Overflow logo and icons and such" />
                    </object>
                    <!-- :src="'http://testingnbackend.somee.com' + customer.imageUrl" -->
                    <!-- <img :src="'http://testingnbackend.somee.com' + customer.imageUrl" class="avatar avatar-sm me-3" /> -->
                    <!-- <img src="../../assets/img/team-2.jpg" class="avatar avatar-sm me-3" alt="user1" /> -->
                  </div>
                  <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm">{{ customer.userName }}</h6>
                    <p class="text-center text-xs text-secondary mb-0">{{ customer.email }}</p>
                  </div>
                </div>
              </td>
              <td>
                <div class="d-flex flex-column text-start">
                  <h6 class="mb-0 text-sm">{{ customer.name }}</h6>
                  <p class="text-xs text-secondary mb-0">{{ customer.phone }}</p>
                </div>
              </td>
              <td>
                <div class="d-flex flex-column text-start">
                  <p class="text-xs font-weight-bold mb-0">{{ getFormattedDate(customer.createAt) }}</p>
                </div>
              </td>
              <td>
                <div class="d-flex flex-column text-start">
                  <p class="text-xs font-weight-bold mb-0">{{ getFormattedDate(customer.updateAt) }}</p>
                </div>
              </td>
              <td class="align-middle">
                <a class="btn btn-link text-dark" @click="toggleEditModal(customer.id)">
                  <i class="fas fa-pencil-alt text-dark me-2" aria-hidden="true"></i>Edit
                </a>
              </td>
              <td class="align-middle">
                <a class="btn btn-link text-danger text-gradient" @click="toggleDeleteModal(customer.id)">
                  <i class="far fa-trash-alt me-2" aria-hidden="true"></i>Delete
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <AddCustomer v-if="modals.addCustomer" />
    <EditCustomer v-if="modals.editCustomer" />
    <deleteCustomer v-if="modals.deleteCustomer" />
  </div>
</template>

<script>
  import ButtonComponent from '@/components/Button.vue';
  import moment from 'moment';
  import { defineAsyncComponent } from 'vue';

  export default {
    name: 'customers-table',
    props: { customers: Object },
    components: {
      ButtonComponent,
      AddCustomer: defineAsyncComponent(() => import('./customers-actions/add-customer')),
      EditCustomer: defineAsyncComponent(() => import('./customers-actions/edit-customer')),
      deleteCustomer: defineAsyncComponent(() => import('./customers-actions/delete-customer')),
    },

    computed: {
      tableProgress() {
        return this.$store.state.CustomersStore.progress.customersList;
      },
      modals() {
        return this.$store.state.CustomersStore.modals;
      },
    },

    methods: {
      getFormattedDate: (date) => moment(date).format('YYYY-MM-DD HH:mm'),

      toggleAddModal() {
        this.$store.commit('CustomersStore/TOGGLE_ADD_CUSTOMER_MODAL');
      },

      toggleEditModal(customerId) {
        this.$store.commit('CustomersStore/SET_CUSTOMER_ID', customerId);
        this.$store.commit('CustomersStore/TOGGLE_EDIT_CUSTOMER_MODAL');
      },

      toggleDeleteModal(customerId) {
        this.$store.commit('CustomersStore/SET_CUSTOMER_ID', customerId);
        this.$store.commit('CustomersStore/TOGGLE_DELETE_CUSTOMER_MODAL');
      },
    },
  };
</script>
